.photos {
  $gap: 20px;
  display: flex;
  justify-content: space-between;

  &__img {
    width: calc(50% - #{$gap / 2 });
    height: 260px;
    object-fit: cover;
    object-position: center;
  }
  &__wide_right {
      
    &__img {

      &:first-child {
        width: calc(33% - #{$gap / 2 });
      }
     
      &:last-child {
        width: calc(67% - #{$gap / 2 });
      }
    }
  }

  &__wide_left {
    &__img {
    
      &:first-child {
        width: calc(67% - #{$gap / 2 });
      }

      &:last-child {
        width: calc(33% - #{$gap / 2 });
      }
    }
  }
}
