.contact {
  display: flex;
  justify-content: space-between;

  &__text {
    font-size: 24px;
    width: 280px;
  }

  &__form {
    width: 380px;
  }

}

@media screen and (max-width: $small-screen-breakpoint) {
  .contact {
    flex-direction: column;
  }

  &__text,
  &__form {
    width: 100%;
  }
}
